* {
    box-sizing: border-box;
    outline: none;
}
body {
    text-rendering: optimizeLegibility;
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: $font-body;
    font-weight: 500;
    color: $color-gray;
    font-size: 16px;
    line-height: 1.3;
    background-color: #fff;
    -webkit-text-size-adjust: none;
    min-height: 100%;
    position: relative;

    // @include media(xl) {
    //     font-size: 14px;
    // }
}
html {
    position: relative;
    min-height: 100%;
    height: 100%;
}
